<template>
  <div class="todo">
    <TodoInput @addItemEvent="addItem" />
    <TodoList :props="todoList" :cnt="cnt" @toggleItemEvent="toggleItem" @resetEvent="resetAll" @deleteEvent="deleteItem" @chageModeEvent="chageMode" @modiEvent="modiItem" @chageSortEvent="chageSort" />
  </div> 
</template>

<script>
import TodoInput from '@/components/TodoInput.vue';
import TodoList from '@/components/TodoList.vue';

export default {
  data(){
    return{
      todoList: [],
      cnt:[],
      idx:0,
      sort:"regi",
    }
  },
  methods:{
    addItem($item){
      let date = new Date();
      let today = date.getFullYear()+"."+date.getMonth()+"."+date.getDate()+" "+ date.getHours()+":"+date.getMinutes()+":"+date.getSeconds();
      let data = {
        todo : $item,
        key : this.idx, 
        date : today,
        isDone: false,
        isMode: false
      }
      this.todoList.push(data);
      //localstorage Ï†ÄÏû•
      localStorage.setItem(data.key, JSON.stringify(data));
      this.chageSort(this.sort);
      this.idx += 1;
      this.getCnt();
    },
    toggleItem($key, $idx, $isDone){
      this.todoList[$idx].isDone= $isDone; 
      let data = this.todoList[$idx];
      localStorage.setItem($key, JSON.stringify(data));
      this.getCnt();
    },
    deleteItem($key, $idx){
      this.todoList.splice($idx, 1); 
      localStorage.removeItem($key);
      this.getCnt();
    },
    chageMode($key, $idx, $isMode){
        console.log($isMode);
      if(!$isMode){
        this.todoList.forEach(item => {
          item.isMode = false; 
        });
      }
      this.todoList[$idx].isMode = !$isMode;  
      let data = this.todoList[$idx];
    },
    modiItem($key, $idx, $todo){
      this.todoList[$idx].isMode = false;
      this.todoList[$idx].todo = $todo; 
      let data = this.todoList[$idx];
      localStorage.setItem($key, JSON.stringify(data));
    },
    resetAll(){
      this.todoList =[];
      this.idx = 0;
      localStorage.clear();
      this.getCnt();
    },
    chageSort($type){
      this.sort = $type;
      if(localStorage.length > 1){
        //Îì±Î°ùÏàú & ÏµúÏã†Ïàú
        if($type == "regi" || $type == "new"){
          this.todoList.sort(function(val1, val2){
            let x = new Date(val1.date).getTime();
            let y = new Date(val2.date).getTime();
            //0 ÎòêÎäî ÏñëÏàòÎ•º ÏùåÏàòÎ•º Î∞òÌôò. 0:ÎèôÏùº, 1 : Ïù¥ÏÉÅ Î∞òÌôòÏùÄ Î¨¥Ìö®. Ïà´Ïûê ÎåÄÏã† >ÏÇ¨Ïö© Í∞ÄÎä•
            if($type == "regi") //Îì±Î°ùÏàú
              return ((x < y) ? -1 : ((x > y) ? 1 : 0));   //Ïò§Î¶ÑÏ∞®Ïàú
            else //ÏµúÏã†Ïàú
              return ((x < y) ? 1 : ((x > y) ? -1 : 0));   //ÎÇ¥Î¶ºÏ∞®Ïàú 
          });
        }
        //Í∞ÄÎÇòÎã§Ïàú 
        if($type == "abc"){
          this.todoList.sort(function(val1, val2){
            let x = val1.todo;
            let y = val2.todo;
            return ((x < y) ? -1 : ((x > y) ? 1 : 0));   //Ïò§Î¶ÑÏ∞®Ïàú
          });
        }
        //ÏôÑÎ£åÏàú 
        if($type == "comp" || $type == "ing"){
          this.todoList.sort(function(val1, val2){
            let x = val1.isDone;
            let y = val2.isDone;
            let z = new Date(val1.date).getTime();
            let w = new Date(val2.date).getTime();
            if($type == "comp") //ÏôÑÎ£åÏàú 
              return ((x < y) ? 1 : ((x > y) ? -1 : ((z > w) ? 1 : ((z > w) ? -1 : 0))));   //ÎÇ¥Î¶ºÏ∞®Ïàú 
            else //ÎπÑÏôÑÎ£åÏàú 
              return ((x < y) ? -1 : ((x > y) ? 1 : ((z > w) ? 1 : ((z > w) ? -1 : 0))));   //ÎÇ¥Î¶ºÏ∞®Ïàú 
          });
        }
      }
    },
    getCnt(){
      //cnt ÏÑ∏ÌåÖ
      let cnt = {
        total : this.todoList.length,
        ing : 0,
        comp : 0
      }
      this.todoList.forEach(item => {
        item.isDone ? cnt.comp++ : cnt.ing++;
      });
      this.cnt = cnt;
    }
  },
  created() {
    //ÌéòÏù¥ÏßÄÍ∞Ä Î°úÎî©Îê†Îïå Ïã§Ìñâ
    //Î°úÏª¨Ïä§ÌÜ†Î¶¨ÏßÄ ÏÑ∏ÌåÖ
    if(localStorage.length > 0){
      //Î°úÏª¨Ïóê Ï†ÄÏû•Îêú Ï†ïÎ≥¥Í∞Ä ÏûàÎäî Í≤ΩÏö∞ Î∂àÎü¨Ïò®Îã§!
      for(let i=0; i<localStorage.length; i++){
        //localStorage.length Í∞Ä Ïã§Ï†ú Î≥¥Îã§ 1ÎßéÏùå
        if (localStorage.key(i) !== "loglevel:webpack-dev-server"){
          let str = localStorage[localStorage.key(i)];
          let obj = JSON.parse(str);
          this.todoList.push(obj);  
        }
      }
      this.chageSort("regi");
      this.getCnt();
      //idx ÎßûÏ∂îÍ∏∞
      this.idx = this.todoList[this.todoList.length-1].key + 1;
    }
  },
  components: { 
    TodoInput,
    TodoList
  }
};
</script>
