<template>
    <div class="bx_ipt">  
        <!-- event v-on or @ÏÇ¨Ïö©  ex) v-on:click @click -->
        <form @submit.prevent="addItem"> 
            <!-- @submit.prevent=... form Í∏∞Î≥∏ÏùÄ ÌéòÏù¥ÏßÄÎ•º Î¶¨Î°úÎìú ÌïòÎäîÎç∞, Í∑∏Í≤É x -->
            <!-- v-model ÏÇ¨Ïö©ÌïòÏó¨  input, textarea ÏóòÎ¶¨Î®ºÌä∏Ïóê ÏñëÎ∞©Ìñ• Îç∞Ïù¥ÌÑ∞ Í∞ÄÎä• -->
            <!-- refÎ•º Ïù¥Ïö©ÌïòÏó¨, input Ï°∞Ïûë -->
            <!-- autofocus ÏÇ¨ÌååÎ¶¨ ÏûëÎèôÏïàÌï® -->
            <input type="text" name="" id="" class="ipt" v-model="todo" ref="ipt" placeholder="Ìï†ÏùºÏùÑ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî!!" autofocus="autofocus" v-focus>
            <button type="submit" class="btn_com">ÏûÖÎ†•</button>
        </form>
    </div>
</template>
<script>
export default {
    data(){
        return{
            todo:""
        }
    },
    methods:{
        addItem(){
            var str = (this.todo).trim();
            if(str !== ""){
                //ÏúÑÎ°ú Í∞í Î≥¥ÎÇ¥Í∏∞! 
                this.$emit("addItemEvent", str); 
                this.todo = "";
            }else{
                this.todo = str;
                alert("Ìï†ÏùºÏùÑ ÏûÖÎ†•Ìï¥ Ï£ºÏÑ∏Ïöî!!");
            }
            this.$refs.ipt.focus();
        }
    }
}
</script>